* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.home {
  width: 100%;
}

.home .top_banner {
  max-width: 100%;
  height: 50vh;
  background: url(http://localhost:3000/EcommerceWebsite/image/slide-1.webp)
    center center no-repeat;
  /*  background: url(/image/slide-1.webp) center center no-repeat;*/
  background-size: cover;
  padding: 50px 60px;
}

.home .top_banner .contant {
  width: 100%;
  position: relative;
  top: 20%;
  padding: 10px 50px;
}

.home .top_banner .contant h3 {
  text-transform: capitalize;
  color: #cd1e76;
  font-weight: 300;
  letter-spacing: 1px;
}

.home .top_banner .contant h2 {
  text-transform: uppercase;
  color: #232323;
  letter-spacing: 1px;
  font-size: 42px;
}

.home .top_banner .contant p {
  color: #232323;
  letter-spacing: 1px;
  margin-top: 10px;
  margin-bottom: 30px;
}

.home .top_banner .contant .link {
  padding: 10px 30px;
  background: #fed700;
  color: #232323;
  text-decoration: none;
  border-radius: 5px;
  transition: 0.5s;
}

.home .top_banner .contant .link:hover {
  color: #fed700;
  background: #cd1e76;
}

.home .trending {
  padding: 20px 30px;
  width: 100%;
}

.home .trending .container {
  max-width: 100%;
  display: flex;
}

.home .trending .container .left_box {
  padding: 10px 20px;
  width: 75%;
}

.home .trending .container .left_box .header {
  display: flex;
  max-width: 100%;
  padding: 10px 10px;
  background: #f6f7f8;
  justify-content: space-between;
}

.home .trending .container .left_box .header .heading h2 {
  text-transform: uppercase;
  font-size: 16px;
  color: #232323;
  margin-top: 5px;
}

.home .trending .container .left_box .header .cate {
  display: flex;
  margin-top: 5px;
}

.home .trending .container .left_box .header .cate h3 {
  text-transform: capitalize;
  margin-right: 10px;
  color: #232323;
  font-size: 14px;
  cursor: pointer;
  transition: 0.5s;
}

.home .trending .container .left_box .header .cate h3:hover {
  color: #cd1e76;
}

.home .trending .container .left_box .products {
  max-width: 100%;
}

.home .trending .container .left_box .products .container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.home .trending .container .left_box .products .container .box {
  width: 195px;
  height: 295px;
  /*padding: 10px 10px;*/
  border: 4px solid #f6f7f8;
  margin-top: 20px;
  margin-left: 20px;
  overflow: hidden;
  border-radius: 5px;
}

.home .trending .container .left_box .products .container .box .img_box {
  display: flex;
}

.home .trending .container .left_box .products .container .box .img_box img {
  width: 130px;
  height: 130px;
  object-fit: cover;
  margin-top: 20px;
}

.home .trending .container .left_box .products .container .box .img_box .icon {
  display: flex;
  flex-direction: column;
  margin-left: -0.5%;
  margin-top: 10px;
  transition: 0.5s;
}

.home
  .trending
  .container
  .left_box
  .products
  .container
  .box:hover
  .img_box
  .icon {
  margin-left: -5%;
}

.home
  .trending
  .container
  .left_box
  .products
  .container
  .box
  .img_box
  .icon
  .icon_box {
  padding: 10px 10px;
  margin-top: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  z-index: 1;
  background: #fff;
  color: #cd1e76;
  transition: 0.5s;
  cursor: pointer;
}

.home
  .trending
  .container
  .left_box
  .products
  .container
  .box
  .img_box
  .icon
  .icon_box:hover {
  color: #fff;
  background: #cd1e76;
}

.home .trending .container .left_box .products .container .box .info {
  padding: 10px 10px;
  margin-left: 10px;
}

.home .trending .container .left_box .products .container .box .info h3 {
  text-transform: uppercase;
  font-size: 14px;
  color: #232323;
  font-weight: 300;
  letter-spacing: 1px;
}

.home .trending .container .left_box .products .container .box .info p {
  color: #cd1e76;
  margin-top: 10px;
  font-size: 14px;
  letter-spacing: 1px;
}

.home .trending .container .left_box .products .container .box .info .btn {
  margin-top: 20px;
  align-items: center;
  padding: 10px 20px;
  margin-left: 20px;
  border: none;
  background: none;
  outline: none;
  background: #fed700;
  color: #232323;
  text-transform: capitalize;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.5s;
}

.home
  .trending
  .container
  .left_box
  .products
  .container
  .box
  .info
  .btn:hover {
  background: #cd1e76;
  color: #f6f7f8;
}
